<template>
  <div>
      <span class="updated">mise à jour à:{{matches.updated}}</span>
        <md-toolbar class="match" v-if="match.statut == 'L'" v-for="match in matches.matches" :key="match.id">
          <div style="width:100%;height:100%;" @click=toComment(match.id)>
              <div style="width:10%;height:100%;float:left;"><img v-bind:src="getLogoClub(match.recevant.slug)" class="logo"></div>
              <div style="width:80%;height:100%;float:left;">
                  <div style="width:100%;float:left;">
                    <div style="width:42%;float:left;">
                      <!-- <span style="float:left;" class="classement">({{match.recevant.classement}}) {{match.recevant.club}}</span> -->
                      <span style="float:left;" class="classement">({{match.recevant.classement}})</span>
                      <span style="float:left;" class="nom">&nbsp{{match.recevant.club}}</span>
                    </div>
                    <div style="width:16%;float:left;">
                      <span style="width:40%;" class="score">{{match.recevant.score}}</span>
                      <span style="width:20%;" class="time" v-if="match.temp === 'MT'">{{match.time}}</span>
                      <span style="width:20%;" class="time" v-else>{{match.time}}'</span>
                      <span style="width:40%;" class="score">{{match.visiteur.score}}</span>
                    </div>
                    <div style="width:42%;float:right;">
                      <!-- <span style="float:right;" class="classement">{{match.visiteur.club}} ({{match.visiteur.classement}})</span> -->
                      <span style="float:right;" class="classement">({{match.visiteur.classement}})</span>
                      <span style="float:right;" class="nom">{{match.visiteur.club}}&nbsp</span>
                    </div>
                  </div>
                  <br>
                  <div style="width:100%;" >
                    <span style="width:40%; float:left;" class="carton">
                      {{match.recevant.nbRed}}
                      <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                      {{match.recevant.nbYellow}}
                      <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                    </span>
                    <span style="width:20%; float:left;" class="ligue">Ligue{{match.ligue}}</span>
                    <span style="width:40%; float:left;" class="carton"> 
                      {{match.visiteur.nbRed}}
                      <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                      {{match.visiteur.nbYellow}}
                      <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                    </span>
                  </div>
              </div>
              <div style="width:10%;float:left;"><img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo"></div>
          </div>
        </md-toolbar>

        <md-toolbar class="match" v-if="match.statut == 'P'" v-for="match in matches.matches" :key="match.id">
          <div style="width:100%" @click=toComment(match.id)>
              <div style="width:10%;float:left;"><img v-bind:src="getLogoClub(match.recevant.slug)" class="logo"></div>
              <div style="width:80%;height:100%;float:left;">
                  <div style="width:100%; float:left;">
                    <div style="width:42%;float:left;">
                      <span style="float:left;" class="classement">({{match.recevant.classement}})</span>
                      <span style="float:left;" class="nom">&nbsp{{match.recevant.club}}</span>
                    </div>
                    <div style="width:16%;float:left;" class="time">
                      {{match.time}}
                    </div>
                    <div style="width:42%;float:right;">
                      <span style="float:right;" class="classement">({{match.visiteur.classement}})</span>
                      <span style="float:right;" class="nom">{{match.visiteur.club}}&nbsp</span>
                    </div>
                  </div>
                  <br>
                  <div style="width:100%;margin:auto" class="ligue">Ligue{{match.ligue}}</div>
              </div>
              <div style="width:10%;float:left;"><img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo"></div>
          </div>
        </md-toolbar>

        <md-toolbar class="match" v-if="match.statut == 'J'" v-for="match in matches.matches" :key="match.id">
         <div style="width:100%;height:100%;" @click=toComment(match.id)>
              <div style="width:10%;height:100%;float:left;"><img v-bind:src="getLogoClub(match.recevant.slug)" class="logo"></div>
              <div style="width:80%;height:100%;float:left;">
                  <div style="width:100%;float:left;">
                    <div style="width:42%;float:left;">
                      <!-- <span style="float:left;" class="classement">({{match.recevant.classement}}) {{match.recevant.club}}</span> -->
                      <span style="float:left;" class="classement">({{match.recevant.classement}})</span>
                      <span style="float:left;" class="nom">&nbsp{{match.recevant.club}}</span>
                    </div>
                    <div style="width:16%;float:left;">
                      <span style="width:40%;" class="score">{{match.recevant.score}}</span>
                      <span style="width:20%;" class="time">-</span>
                      <span style="width:40%;" class="score">{{match.visiteur.score}}</span>
                    </div>
                    <div style="width:42%;float:right;">
                      <!-- <span style="float:right;" class="classement">{{match.visiteur.club}} ({{match.visiteur.classement}})</span> -->
                      <span style="float:right;" class="classement">({{match.visiteur.classement}})</span>
                      <span style="float:right;" class="nom">{{match.visiteur.club}}&nbsp</span>
                    </div>
                  </div>
                  <br>
                  <div style="width:100%;" >
                    <span style="width:40%; float:left;" class="carton">
                      {{match.recevant.nbRed}}
                      <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                      {{match.recevant.nbYellow}}
                      <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                    </span>
                    <span style="width:20%; float:left;" class="ligue">Ligue{{match.ligue}}</span>
                    <span style="width:40%; float:left;" class="carton"> 
                      {{match.visiteur.nbRed}}
                      <img src="static/img/commentairesIcons/rouge.png" style="width:15px;height:15px;"> 
                      {{match.visiteur.nbYellow}}
                      <img src="static/img/commentairesIcons/jaune.png" style="width:15px;height:15px;">
                    </span>
                  </div>
              </div>
              <div style="width:10%;float:left;"><img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo"></div>
          </div>
        </md-toolbar> 
  </div>
</template>

<script>
var debounce = require('lodash.debounce')

export default {
  name: 'matchs',

  data () {
    return {
      matches: [],
      match: '',
      id: ''
    }
  },

  methods: {
    toComment (ID) {
      this.$localStorage.set('id', ID)
      this.$router.push('Commentaires')
    },
    getLogoClub (value) {
      return 'static/img/clubs/' + value + '.svg'
    },
    getTime (match) {
      if (match.status === 'L') return match.time + '\''
      else if (match.status === 'P') return 'P'
      else if (match.status === 'J') return 'J'
    },
    update () {
      this.matches = this.$store.state.matches
    },
    updateMatches: debounce(function () {
      this.update()
    }, 1000)
  },

  watch: {
    matches: function () {
      this.updateMatches()
    }
  },

  created: function () {
    this.$localStorage.set('id', '1')
    window.addEventListener('online', this.updateMatches)
  },

  mounted: function () {
    console.log('match mounted')
    this.update()
  }
}
</script>

<style>

  html, body {
    width:100%;
    height: 100%;
    margin:0;
    padding:0;
  }

  .updated{
    float: right;
  }
  
  .match {
    padding:10px 0;
  }
  .score{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(8px + 1vw + 1vh); 
    padding: 5px 0;
  }
  .time{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(8px + 0.6vw + 0.4vh); 
    padding: 5px 0;
    text-align: center;
  }
  .nom{
    word-break:break-all;
    font-size: calc(7px + 0.6vw + 0.4vh);
  }
  
  .logo{
    height: calc(5px + 8vw);
    width: calc(5px + 8vw);
    padding: 0;
    max-width: 80px;
    max-height: 80px;
  }

  .classement{
    font-size: calc(5px + 1vw);
  }
  .ligue{
    font-size: calc(7px + 1vw);
  }
  .carton{
    font-size: calc(7px + 1vw);
  }
  
</style>



